<template>
  <section class="step-10">
    <div class="add-place-des">
      <h4>Owner Info</h4>
    </div>
    <div class="step-10-body">
      <div class="row">
        <div class="col-6">
          <ValidationObserver v-slot="{ invalid }">
            <form @submit.prevent="" class="form">
              <div class="check-owner">
                <label class="check-container">
                  <span class="check-txt"> I own the property </span>
                  <input type="checkbox" v-model="iOwnTheProperty" />
                  <span class="checkmark"></span>
                </label>

                <label class="check-container">
                  <span class="check-txt"> I will recieve renting money </span>
                  <input type="checkbox" v-model="moneyReceiver" />
                  <span class="checkmark"></span>
                </label>
              </div>

              <div class="owner-info">
                <ValidationProvider
                  name="ownerName"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <div class="form-input-wrapper">
                    <label for="" v-if="!iOwnTheProperty">Owner name</label>
                    <label for="" v-if="iOwnTheProperty">Your name</label>
                    <div class="form-input-element">
                      <input v-model="ownerName" type="text" />
                    </div>

                    <span class="form-error">{{ errors[0] }}</span>
                  </div>
                </ValidationProvider>

                <ValidationProvider
                  name="ownerName"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <div class="form-input-wrapper">
                    <label for="" v-if="!iOwnTheProperty"
                      >Owner phone number</label
                    >
                    <label for="" v-if="iOwnTheProperty"
                      >Your phone number</label
                    >
                    <div class="form-input-element">
                      <input v-model="ownerPhoneNumber" type="text" />
                    </div>

                    <span class="form-error">{{ errors[0] }}</span>
                  </div>
                </ValidationProvider>
              </div>

              <div class="info-provider" v-if="!iOwnTheProperty">
                <ValidationProvider
                  name="infoProviderName"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <div class="form-input-wrapper">
                    <label for="">Your name</label>
                    <div class="form-input-element">
                      <input v-model="infoProviderName" type="text" />
                    </div>

                    <span class="form-error">{{ errors[0] }}</span>
                  </div>
                </ValidationProvider>

                <ValidationProvider
                  name="infoProviderPhoneNumber"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <div class="form-input-wrapper">
                    <label for="">Your phone number</label>
                    <div class="form-input-element">
                      <input v-model="infoProviderPhoneNumber" type="text" />
                    </div>

                    <span class="form-error">{{ errors[0] }}</span>
                  </div>
                </ValidationProvider>
              </div>

              <div class="bank-info">
                <ValidationProvider
                  name="mBokAccountNumber"
                  rules="required|numeric"
                  v-slot="{ errors }"
                >
                  <div class="form-input-wrapper">
                    <label for="" v-if="!iOwnTheProperty">
                      <span v-if="moneyReceiver">Your</span>
                      <span v-if="!moneyReceiver">Owner</span>

                      mBok account number</label
                    >
                    <label for="" v-if="iOwnTheProperty">
                      <span v-if="moneyReceiver">Your</span>
                      <span v-if="!moneyReceiver">Money receiver</span>
                      mBok account number</label
                    >
                    <div class="form-input-element">
                      <input v-model="mBokAccountNumber" type="number" />
                    </div>

                    <span class="form-error">{{ errors[0] }}</span>
                  </div>
                </ValidationProvider>

                <ValidationProvider
                  name="mBokAccountName"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <div class="form-input-wrapper">
                    <label for="" v-if="!iOwnTheProperty"
                      ><span v-if="moneyReceiver">Your</span>
                      <span v-if="!moneyReceiver">Owner</span>
                      mBok account name</label
                    >
                    <label for="" v-if="iOwnTheProperty">
                      <span v-if="moneyReceiver">Your</span>
                      <span v-if="!moneyReceiver">Money receiver</span>
                      mBok account name</label
                    >
                    <div class="form-input-element">
                      <input v-model="mBokAccountName" type="text" />
                    </div>

                    <span class="form-error">{{ errors[0] }}</span>
                  </div>
                </ValidationProvider>
              </div>

              <div class="notes">
                <div class="form-input-wrapper">
                  <label for="">Notes</label>
                  <div class="form-input-element">
                    <textarea id="w3review" name="notes" v-model="notes">
                    </textarea>
                  </div>
                </div>
              </div>

              <Navigation :valid="!invalid" />
            </form>
          </ValidationObserver>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Navigation from "./Navigation.vue";
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";
import { numeric, required } from "vee-validate/dist/rules";

extend("required", {
  validate(value) {
    return {
      required: true,
      valid: ["", " ", null, undefined].indexOf(value) === -1,
      message: "This field is required",
    };
  },
  computesRequired: true,
});

extend("numeric", {
  ...numeric,
  message: "This field only accept numbers",
});

export default {
  components: { Navigation, ValidationObserver, ValidationProvider, extend },
  data() {
    return {
      moneyReceiver: false,
    };
  },
  computed: {
    iOwnTheProperty: {
      get() {
        return this.$store.state.steps.step10.iOwnTheProperty;
      },
      set(value) {
        this.$store.commit("setIOwnTheProperty", { iOwnTheProperty: value });
      },
    },
    ownerName: {
      get() {
        return this.$store.state.steps.step10.ownerName;
      },
      set(value) {
        this.$store.commit("setOwnerName", { ownerName: value });
      },
    },
    ownerPhoneNumber: {
      get() {
        return this.$store.state.steps.step10.ownerPhoneNumber;
      },
      set(value) {
        this.$store.commit("setOwnerPhoneNumber", { ownerPhoneNumber: value });
      },
    },
    mBokAccountNumber: {
      get() {
        return this.$store.state.steps.step10.bankInfo.mBokAccountNumber;
      },
      set(value) {
        this.$store.commit("setmBokAccountNumber", {
          mBokAccountNumber: value,
        });
      },
    },
    mBokAccountName: {
      get() {
        return this.$store.state.steps.step10.bankInfo.mBokAccountName;
      },
      set(value) {
        this.$store.commit("setmBokAccountName", { mBokAccountName: value });
      },
    },
    mBokAccountBranch: {
      get() {
        return this.$store.state.steps.step10.bankInfo.mBokAccountBranch;
      },
      set(value) {
        this.$store.commit("setmBokAccountBranch", {
          mBokAccountBranch: value,
        });
      },
    },
    infoProviderName: {
      get() {
        return this.$store.state.steps.step10.infoProviderInfo.name;
      },
      set(value) {
        this.$store.commit("setInfoProviderName", { infoProviderName: value });
      },
    },
    infoProviderPhoneNumber: {
      get() {
        return this.$store.state.steps.step10.infoProviderInfo.phoneNumber;
      },
      set(value) {
        this.$store.commit("setInfoProviderPhoneNumber", {
          infoProviderPhoneNumber: value,
        });
      },
    },
    notes: {
      get() {
        return this.$store.state.steps.step10.notes;
      },
      set(value) {
        this.$store.commit("setNotes", { notes: value });
      },
    },
  },
};
</script>
<style lang="scss">
/* Customize the label (the container) */
.check-container {
  display: block;
  position: relative;
  padding-left: 1.7rem;
  cursor: pointer;
  font-size: 1.2rem !important;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin-bottom: 0.5rem;
}

/* Hide the browser's default checkbox */
.check-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 5px;
  left: 0;
  height: 20px;
  width: 20px;
  border: 1px solid $text;
  border-radius: 5px;
}

/* On mouse-over, add a grey background color */
.check-container:hover input ~ .checkmark {
  border-color: $textDark;
}

/* When the checkbox is checked, add a blue background */
.check-container input:checked ~ .checkmark {
  background-color: $primary;
  border: none;
  color: $white;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.check-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.check-container .checkmark:after {
  left: 7px;
  top: 4px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.check-owner {
  margin-bottom: 1rem;
}
</style>
